.tui-pagination {
  height: 40px;
  display: flex;
  justify-content: center;
  margin: 40px 0;
  @media screen and (min-width: $tablet) {
    margin-top: 50px;
    margin-bottom: 60px;
  }
}

.tui-page-btn.tui-first.hide-first,
.tui-page-btn.tui-first.tui-is-disabled,
.tui-page-btn.tui-last.hide-last,
.tui-page-btn.tui-last.tui-is-disabled,
.tui-page-btn.tui-next-is-ellip.change-next,
.tui-page-btn.tui-prev-is-ellip.change-prev {
  display: none;
}

@media screen and (min-width: $tablet) {
  .tui-page-btn.tui-next-is-ellip.change-next,
  .tui-page-btn.tui-prev-is-ellip.change-prev {
    display: flex;
  }
}

.tui-pagination .tui-next {
  margin-left: 0;
}

.tui-pagination .tui-prev {
  margin-right: 0;
}

.tui-pagination .tui-page-btn {
  display: flex;
  border: none;
  align-items: center;
  justify-content: center;
  padding: 0;
  width: 40px;
  height: 40px;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  border-radius: 5px;
  color: var(--second-text-color);
  font-family: Roboto;
  transition: all 250ms ease-in-out;
  &:hover {
    background: var(--btn-pagination-color);
    color: var(--first-text-color);
  }
}

.tui-pagination .tui-is-selected {
  font-weight: 500;
  cursor: default;
  color: var(--first-text-color);
  background: var(--btn-pagination-color);
  border-color: var(--btn-pagination-color);
}
