// .tui-pagination strong {
//   color: var(--first-text-color);
//   background: var(--btn-pagination-color);
//   cursor: default;
// }

.tui-pagination .tui-is-disabled:hover {
  background: transparent;
  border: none;
}
.tui-pagination .hide-prev,
.tui-pagination .hide-next {
  background: var(--footer-background-color);
}

.tui-pagination .tui-prev,
.tui-pagination .tui-next {
  color: var(--second-text-color);
  background: var(--footer-background-color);
}
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: var(--second-text-color);
}
.tui-pagination .tui-prev-is-ellip:hover,
.tui-pagination .tui-next-is-ellip:hover,
.tui-pagination .hide-prev:hover,
.tui-pagination .hide-next:hover {
  background: var(--btn-pagination-color);
}

.tui-pagination .tui-prev-is-ellip:hover > .tui-ico-ellip,
.tui-pagination .tui-next-is-ellip:hover > .tui-ico-ellip,
.tui-pagination .hide-prev:hover > .tui-ico-prev,
.tui-pagination .hide-next:hover > .tui-ico-next {
  fill: var(--first-text-color);
}

.tui-pagination .tui-is-selected:hover {
  background: var(--btn-pagination-color);
}

.tui-pagination .tui-ico-ellip {
  margin-top: 0;
}

.tui-pagination .tui-first-child.tui-is-selected {
  border-left: none;
}

.tui-pagination .tui-ico-ellip {
  width: 11px;
  fill: var(--second-text-color);
  transition: all 250ms ease-in-out;
}

.tui-pagination .tui-prev,
.tui-pagination .tui-next {
  width: 40px;
  height: 40px;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  fill: var(--second-text-color);
  transition: all 250ms ease-in-out;
}
