.movies-list {
  display: flex;
  flex-wrap: wrap;
  min-width: 280px;

  padding-top: 60px;
}

// .movie-card {
//   outline: 1px solid red;
// }

.movie-item {
  display: block;
}

@media screen and (max-width: $max-mobile) {
  .movies-list {
    padding-top: 20px;

    .movie-item {
      width: 100%;
    }
    .movie-item:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}

@media screen and (min-width: $tablet) and (max-width: $max-tablet) {
  .movies-list {
    padding-left: 55px;
    padding-right: 55px;

    .movie-item {
      width: calc((100% - 30px) / 2);
    }

    .movie-item:nth-child(odd) {
      margin-right: 30px;
    }

    .movie-item:not(:nth-last-child(-n + 2)) {
      margin-bottom: 30px;
    }
  }
}

@media screen and (min-width: $desktop) {
  .movies-list {
    width: 100%;
    padding-left: 51px;
    padding-right: 51px;
  }

  .card-img {
    height: 411px;
  }

  .movie-item {
    width: calc((100% - 60px) / 3);
  }

  .movie-item:not(:nth-last-child(-n + 3)) {
    margin-bottom: 30px;
  }

  .movie-item:not(:nth-child(3n)) {
    margin-right: 30px;
  }
}
